.index-link {
  color: #006fbc;
  text-decoration: none;
}

.index-list {
  margin: 0;
  padding: 0 0 0 20px;
}

.index-cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  padding: 24px 0;
}

.index-card {
  display: flex;
  flex-direction: column;
  gap: 32px;
  padding: 24px 16px;
  border-radius: 12px;
  background-color: rgba(1, 144, 219, 0.08);
}
.index-card__title-wrapper {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.index-card__title {
  font-size: 20px;
  font-weight: 500;
  line-height: 24px;
}
.index-card__button {
  width: fit-content;
}

.baseline-table {
  display: grid;
  grid-template-columns: repeat(3, max-content);
  row-gap: 16px;
  column-gap: 32px;
  width: fit-content;
}
.baseline-table__row {
  display: flex;
  gap: 32px;
}
.baseline-table__row-block {
  display: flex;
  flex-direction: column;
  gap: 4px;
  min-width: 128px;
  width: fit-content;
}
.baseline-table__row-block_grow .baseline-table__row-text {
  color: #1d8722;
}
.baseline-table__row-title {
  color: #5c5c5c;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 12px;
  line-height: 16px;
}
.baseline-table__row-text {
  min-height: 24px;
}

.baseline-table-description {
  width: fit-content;
  margin-top: 16px;
  padding-top: 8px;
  border-top: 1px solid #d6d6d6;
  color: #5c5c5c;
  font-size: 12px;
  line-height: 16px;
}

.main-table {
  border-collapse: collapse;
  table-layout: fixed;
}
.main-table .border-right {
  border-right: 1px solid #d6d6d6;
}
.main-table .center {
  text-align: center;
}
.main-table .status-ok {
  background-color: rgba(43, 153, 39, 0.08);
}
.main-table .status-error {
  background-color: rgba(255, 59, 56, 0.08);
}
.main-table .text-error {
  color: #db2835;
}
.main-table thead th {
  padding: 12px;
  text-align: left;
  color: #1f1f1f;
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
  text-align: center;
}
.main-table thead th.border-right {
  border-right: 1px solid #d6d6d6;
}
.main-table thead tr:nth-child(2) th {
  border-bottom: 2px solid #d6d6d6;
  font-size: 14px;
  line-height: 20px;
}
.main-table thead tr:nth-child(2) .day-column {
  width: 6.11%;
}
.main-table thead tr:nth-child(2) .content-column {
  width: 13.61%;
}
.main-table tbody td {
  padding: 12px;
  border-bottom: 1px solid #d6d6d6;
  color: #1f1f1f;
  font-size: 14px;
  line-height: 20px;
  text-align: right;
}

.masters-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.master {
  display: flex;
  gap: 8px;
  width: 400px;
}
.master__avatar {
  overflow: hidden;
  position: relative;
  width: 48px;
  height: 48px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background-color: #6ec9cb;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
}
.master__avatar-img {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  object-fit: cover;
}
.master__content {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 4px 0;
}
.master__text-block {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.master__percent {
  font-weight: 600;
}
.master__progress {
  width: 100%;
  height: 8px;
  border-radius: 99px;
  background-color: rgba(0, 0, 0, 0.08);
}
.master__progress-bar {
  width: var(--percent-value);
  height: 8px;
  border-radius: 99px;
  background-color: #5bb2e0;
}

.total-time {
  display: flex;
  gap: 32px;
}
.total-time__block {
  display: flex;
  flex-direction: column;
  gap: 4px;
  min-width: 128px;
}
.total-time__text {
  overflow: hidden;
  color: #5c5c5c;
  text-overflow: ellipsis;
  font-size: 12px;
  font-weight: 400;
  line-height: 16px;
}

.welcome-list {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 8px 0;
  margin: 0;
}
.welcome-list__item {
  display: flex;
  gap: 16px;
}
.welcome-list__item-icon {
  flex-shrink: 0;
  width: 12px;
  height: 12px;
  margin-top: 6px;
}
.welcome-list__item-icon.donut {
  color: rgb(220, 48, 48);
}
.welcome-list__item-icon.triangle {
  color: rgb(0, 135, 204);
}
.welcome-list__item-icon.diamond {
  color: rgb(0, 164, 59);
}
.welcome-list__item-icon.circle {
  color: #857ac5;
}
.welcome-list__item-icon.star {
  color: #ffc000;
}
.welcome-list__item-icon.trapeze {
  color: #dc3030;
}
.welcome-list__item-icon.dots {
  color: #0087cc;
}
.welcome-list__content {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.welcome-list__title {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.welcome-list__link {
  color: #016aa2;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
}

.welcome-webinar-link {
  display: flex;
  flex-direction: column;
  padding-top: 24px;
}
.welcome-webinar-link__link {
  width: fit-content;
}

.welcome-homework {
  display: flex;
  flex-direction: column;
  padding-top: 24px;
}
.welcome-homework__content {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 16px;
  border-radius: 8px;
  background: rgba(1, 144, 219, 0.08);
}
.welcome-homework__title {
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
.welcome-homework__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
}

.welcome-links {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 8px;
  padding: 24px 0 0;
  margin: 0;
  list-style: none;
}

.welcome-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4px;
  padding: 12px 8px;
  border-radius: 8px;
  border: 1px solid rgba(1, 144, 219, 0.48);
  text-decoration: none;
  transition: all 0.2s;
}
.welcome-link__icon {
  width: 24px;
  height: 24px;
  color: #0190db;
  transition: all 0.2s;
}
.welcome-link__text {
  font-size: 14px;
  line-height: 20px;
  color: #0190db;
  transition: all 0.2s;
}
.welcome-link:hover {
  border-color: #006fbc;
}
.welcome-link:hover .welcome-link__icon {
  color: #006fbc;
}
.welcome-link:hover .welcome-link__text {
  color: #006fbc;
}

.header {
  position: fixed;
  z-index: 900;
  left: 0;
  top: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  width: 272px;
  padding: 24px 16px 48px;
  border-right: 1px solid #d6d6d6;
  background-color: #fff;
}
@media (max-width: 1055px) {
  .header {
    right: 0;
    bottom: auto;
    width: auto;
    display: grid;
    grid-template-columns: 48px 1fr;
    padding: 16px 0;
    margin: 0 24px;
    border-right: none;
    border-bottom: 1px solid #d6d6d6;
  }
}
@media (max-width: 767px) {
  .header {
    margin: 0;
    padding: 8px;
    grid-template-columns: 48px 1fr;
  }
}

.header-burger {
  display: none;
  visibility: hidden;
}
.header-burger.open .header-burger__icon.burger {
  display: none;
}
.header-burger.open .header-burger__icon.cross {
  display: block;
}
.header-burger .header-burger__icon.cross {
  display: none;
}
@media (max-width: 1055px) {
  .header-burger {
    visibility: visible;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    padding: 0;
    margin: 0 8px 0 0;
    border-radius: 8px;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  .header-burger__icon {
    width: 24px;
    height: 24px;
    fill: #5c5c5c;
  }
}
@media (max-width: 767px) {
  .header-burger {
    position: relative;
  }
  .header-burger:not(.open):after {
    content: "";
    position: absolute;
    right: 4px;
    top: 4px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }
}

.title-wrapper {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 8px;
  margin-bottom: 16px;
}
@media (max-width: 1055px) {
  .title-wrapper {
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 48px;
    padding: 0;
    margin-bottom: 0;
  }
}

.title {
  display: none;
  margin: 0;
  color: #1f1f1f;
  font-size: 24px;
  font-weight: 500;
  line-height: 32px;
}
.title__sub-text {
  margin: 0;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
}

.title-logo {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 4px;
}
@media (max-width: 1055px) {
  .title-logo {
    margin-top: 6px;
    margin-bottom: auto;
  }
}
@media (max-width: 767px) {
  .title-logo {
    margin-top: 8px;
  }
}
.title-logo__icon {
  width: 192px;
  height: 32px;
}
@media (max-width: 1055px) {
  .title-logo__icon {
    width: 168px;
    height: 28px;
  }
}
@media (max-width: 767px) {
  .title-logo__icon {
    width: 144px;
    height: 24px;
  }
}

.header-back {
  display: flex;
  align-items: center;
  gap: 4px;
  min-height: 36px;
  padding: 0 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  color: #0190db;
  text-decoration: none;
}
.header-back__icon {
  width: 24px;
  height: 24px;
}

.nav {
  overflow: auto;
  width: calc(100% + 32px);
  margin-left: -16px;
  padding: 0 16px;
  margin-bottom: 16px;
}
.nav::-webkit-scrollbar {
  width: 4px;
}
.nav::-webkit-scrollbar-thumb {
  border-radius: 10px;
  border: 2px solid #d6d6d6;
}
.nav::-webkit-scrollbar-track {
  background-color: #fff;
  border-radius: 10px;
}
@media (max-width: 1055px) {
  .nav {
    position: fixed;
    top: 113px;
    left: 0;
    right: 0;
    bottom: 0;
    width: auto;
    margin-left: 0;
    margin-bottom: 0;
    padding: 16px 32px;
    background-color: #fff;
  }
}
@media (max-width: 767px) {
  .nav {
    top: 57px;
    padding: 16px 16px 84px;
  }
}

.nav_list {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
}

.nav-link {
  display: flex;
  align-items: center;
  padding: 16px 16px 16px 8px;
  border-bottom: 1px solid #d6d6d6;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.43;
  text-decoration: none;
  color: #5c5c5c;
}
.nav-link:hover {
  color: #1f1f1f;
}
.nav-link_active {
  padding-bottom: 13px;
  border-bottom: 4px solid, #0190db;
  color: #1f1f1f;
}

.title-h2 {
  padding: 4px 0 16px;
}
.title-h2__text {
  margin: 0;
  color: #1f1f1f;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.3;
}
.title-h2__label {
  margin-top: 4px;
  color: #1f1f1f;
  font-size: 16px;
  line-height: 24px;
}

.title-h3 {
  padding: 24px 0 8px;
}
@media (max-width: 767px) {
  .title-h3 {
    padding-top: 8px;
  }
}
.title-h3__text {
  margin: 0;
  color: #1f1f1f;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
}

.status {
  display: flex;
  align-items: center;
  gap: 4px;
  flex-wrap: nowrap;
  width: fit-content;
  height: 24px;
  padding: 4px 8px;
  border-radius: 360px;
  background-color: rgba(1, 144, 219, 0.12);
}
.status_ok {
  background-color: rgba(43, 153, 39, 0.12);
}
.status_warning {
  background-color: rgba(245, 184, 0, 0.12);
}
.status_error {
  background-color: rgba(255, 59, 56, 0.12);
}
.status_calendar {
  background-color: rgba(1, 144, 219, 0.12);
}
.status_calendar .status__text {
  color: #00539d;
}
.status__icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}
.status__icon_ok {
  color: #2b9927;
}
.status__icon_warning {
  color: #e0a800;
}
.status__icon_error {
  color: #ff3b38;
}
.status__icon_calendar {
  color: #0190db;
}
.status__text {
  flex-shrink: 0;
  padding: 0 4px;
  font-size: 12px;
  line-height: 16px;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  margin: 0;
  border-width: 1px;
  border-style: solid;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.2s;
}
.button__icon {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  color: #fff;
  opacity: 0.64;
}
@media (max-width: 767px) {
  .button {
    padding: 13px 23px !important;
    font-size: 16px;
    line-height: 24px;
  }
}
.button.s {
  padding: 7px 23px;
}
.button.l {
  padding: 11px 31px;
}
.button.m {
  padding: 13px 31px;
  font-size: 16px;
}
.button.primary {
  border-color: #0190db;
  background-color: #0190db;
  color: #fff;
}
.button.primary:hover:not(:disabled) {
  border-color: #006fbc;
  background-color: #006fbc;
}
.button.primary:active:not(:disabled) {
  border-color: #00539d;
  background-color: #00539d;
}
.button.primary:disabled {
  border-color: #0190db;
  background-color: #0190db;
  opacity: 0.48;
}
.button.secondary {
  background-color: transparent;
  border-color: rgba(1, 144, 219, 0.48);
  color: #1f1f1f;
}
.button.secondary:hover:not(:disabled) {
  color: #0190db;
}
.button.secondary:active:not(:disabled) {
  border-color: #00539d;
  color: #006fbc;
}
.button.secondary:disabled {
  border-color: rgba(1, 144, 219, 0.48);
  opacity: 0.48;
}

* {
  box-sizing: border-box;
}

html {
  -webkit-font-smoothing: antialiased;
  min-height: 100%;
}

body {
  min-height: 100%;
  font-size: 16px;
  font-weight: 400;
  font-family: "SF Pro Display", "Helvetica", "Arial", sans-serif;
  line-height: 1.5;
  color: #1f1f1f;
  background-color: #fff;
}

.main-content {
  display: flex;
  flex-direction: column;
  gap: 8px;
  max-width: 784px;
  margin-left: 272px;
  padding: 32px;
}
@media (max-width: 1055px) {
  .main-content {
    margin-left: 0;
    margin-top: 112px;
    padding: 16px 32px;
  }
}
@media (max-width: 767px) {
  .main-content {
    padding: 16px;
    margin-top: 57px;
  }
}

.hidden {
  display: none !important;
}

.transparent {
  opacity: 0 !important;
}

.body-lock {
  overflow: hidden;
}